import React from 'react';
import styled from 'styled-components';
import { css } from '@emotion/core';
import sunriseAndSunsetData from './sunrise-sunset.json';
import { ThemeProvider } from 'emotion-theming';
import WeatherCard from './WeatherCard';
import useWeatherApi from './useWeatherApi';
import WeatherSetting from './WeatherSetting';
import { findLocation } from './utils';

const getMoment = (locationName) => {
  // STEP 2
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  );

  // STEP 3 null
  if (!location) return null;

  // STEP 4
  const now = new Date();

  // STEP 5 "2019-10-08" 
  const nowDate = Intl.DateTimeFormat('zh-TW', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  })
    .format(now)
    .replace(/\//g, '-');

  // STEP 6
  const locationDate =
    location.time && location.time.find((time) => time.dataTime === nowDate);

  // STEP 7TimeStamp
  const sunriseTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).getTime();
  const sunsetTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).getTime();
  const nowTimeStamp = now.getTime();

  // STEP 8
  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp ? 'day' : 'night';
};
const theme = {
  light: {
    backgroundColor: '#ededed',
    foregroundColor: '#f9f9f9',
    titleColor: '#212121',
    temperatureColor: '#757575',
    textColor: '#828282',
  },
  dark: {
    backgroundColor: '#1F2022',
    foregroundColor: '#121416',
    titleColor: '#f9f9fa',
    temperatureColor: '#dddddd',
    textColor: '#cccccc',
  },
};

const Weather = () => {
  // 
  // localStorage.setItem(keyName, keyValue);

  // 
  // localStorage.getItem(keyName);

  // 
  // localStorage.removeItem(keyName);

  // 
  // localStorage.clear();

  //  localStorage  cityName storageCity
  const storageCity = localStorage.getItem('cityName');
  //  storageCity  currentCity  ''
  const [currentCity, setCurrentCity] = React.useState(storageCity || '');
  const currentLocation = findLocation(currentCity) || {};

  const [weatherElement, fetchData] = useWeatherApi(currentLocation);
  const [currentTheme, setCurrentTheme] = React.useState('light');
  const [currentPage, setCurrentPage] = React.useState('WeatherCard');



  //  currentCity  localStorage 
  React.useEffect(() => {
    localStorage.setItem('cityName', currentCity);
    // dependencies  currentCity
  }, [currentCity]);

  // const moment = React.useMemo(() => getMoment(weatherElement.locationName), [weatherElement.locationName]);
  const moment = React.useMemo(() => getMoment(currentLocation.sunriseCityName), [
    currentLocation.sunriseCityName,
  ]);



  //  moment 
  React.useEffect(() => {
    setCurrentTheme(moment === 'day' ? 'light' : 'dark');
    //  moment  dependencies 
  }, [moment]);

  return (
    <ThemeProvider theme={theme[currentTheme]}>
      < Container >
        {currentPage === 'WeatherCard' && (
          <WeatherCard
            weatherElement={weatherElement}
            moment={moment}
            fetchData={fetchData}
            setCurrentPage={setCurrentPage}
            cityName={currentLocation.cityName}
          />
        )}

        {currentPage === 'WeatherSetting' && (
          <WeatherSetting
            setCurrentPage={setCurrentPage}
            cityName={currentLocation.cityName}
            setCurrentCity={setCurrentCity}
            setCurrentPage={setCurrentPage}
          />
        )}
      </Container >
    </ThemeProvider >
  )
}
export default Weather;





const buttonDefault = (props) => css`
  display: block;
  width: 120px;
  height: 30px;
  font-size: 14px;
  background-color: transparent;
  color: ${props.theme === 'dark' ? '#dadada' : '#212121'};
`;
const rejectButton = styled.button`
  ${buttonDefault}
  background-color: red;
`
const acceptButton = styled.button`
  ${buttonDefault}
  background-color: green;
`
const Container = styled.div`
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 360px;
  width:100%;
  margin:15px auto;
  box-sizing:border-box;
  background-color:${({ theme }) => theme.backgroundColor};
`;
